.App {
  width: 100vw;
  height: 100vh;
  overflow: auto;
}

.image-wrapper {
  display: flex;
  width: 100%;
}

.image-wrapper .img {
  flex-basis: 100%;
  flex-shrink: 1;
  flex-grow: 1;

  max-height: calc(100vh - 200px);
}

.image-wrapper .img canvas {
  width: 100%;
}

.brush {
  position: fixed;
  pointer-events: none;
  box-sizing: border-box;
  border: solid 1px #333;
  display: none;
  border-radius: 50%;
}

.image-wrapper:hover .brush {
  display: block;
}

.image-wrapper .target-image:hover .brush {
  display: none;
}
